import React, { useState } from 'react';
import logo from '../assets/img/ico_logo.png';

function Login({ onLogin }) {
  const [isSignup, setIsSignup] = useState(false);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState('');

  const handleSubmit = (e) => {
    e.preventDefault();
    setError('');

    //  
    if (!email || !password) {
      setError('  .');
      return;
    }

    if (isSignup && password !== confirmPassword) {
      setError('  .');
      return;
    }

    if (isSignup) {
      // 
      const users = JSON.parse(localStorage.getItem('users') || '[]');
      
      //    
      if (users.find(u => u.email === email)) {
        setError('  .');
        return;
      }

      //   
      users.push({ email, password });
      localStorage.setItem('users', JSON.stringify(users));
      
      //  
      localStorage.setItem('currentUser', email);
      onLogin(email);
    } else {
      // 
      const users = JSON.parse(localStorage.getItem('users') || '[]');
      const user = users.find(u => u.email === email && u.password === password);
      
      if (user) {
        localStorage.setItem('currentUser', email);
        onLogin(email);
      } else {
        setError('    .');
      }
    }
  };

  //   ()
  const handleTestLogin = () => {
    const testEmail = 'test@test.com';
    localStorage.setItem('currentUser', testEmail);
    onLogin(testEmail);
  };

  return (
    <div className="login-container">
      <div className="login-box">
        <div className="login-header">
          
          <h1 className="login-title">Todoit<img src={logo} alt="Todoit Logo" className="login-logo" /></h1>
          <p className="login-subtitle">
            {isSignup ? '  ' : ' '}
          </p>
        </div>

        <form onSubmit={handleSubmit} className="login-form">
          {error && <div className="login-error">{error}</div>}
          
          <div className="login-form-group">
            <label htmlFor="email" className="login-label"></label>
            <input
              id="email"
              type="email"
              className="login-input"
              placeholder="example@email.com"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              autoComplete="email"
            />
          </div>

          <div className="login-form-group">
            <label htmlFor="password" className="login-label"></label>
            <input
              id="password"
              type="password"
              className="login-input"
              placeholder=""
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              autoComplete={isSignup ? "new-password" : "current-password"}
            />
          </div>

          {isSignup && (
            <div className="login-form-group">
              <label htmlFor="confirmPassword" className="login-label"> </label>
              <input
                id="confirmPassword"
                type="password"
                className="login-input"
                placeholder=""
                value={confirmPassword}
                onChange={(e) => setConfirmPassword(e.target.value)}
                autoComplete="new-password"
              />
            </div>
          )}

          <button type="submit" className="btn-common btn-primary">
            {isSignup ? '' : ''}
          </button>

          <button type="button" className="btn-common btn-gray" onClick={handleTestLogin}>
              ( )
          </button>
        </form>

        <div className="login-footer">
          <button 
            type="button"
            className="login-toggle-btn"
            onClick={() => {
              setIsSignup(!isSignup);
              setError('');
              setConfirmPassword('');
            }}
          >
            {isSignup ? '  ? ' : ' ? '}
          </button>
        </div>
      </div>
    </div>
  );
}

export default Login;
